import axios from "axios"
const url ='https://gemjson.herokuapp.com';
console.log(url)

debugger
export const get = () => (dispatch) => {
    axios.get(`${url}/gemstone`).then((res) => {
        dispatch({
            type: 'Get-GEMSTONE',
            payload:  res.data,
        })
    })
}
export const getById = (id) => (dispatch) => {
    axios.get(`${url}/gemstone`).then((res) => {
        let data = res.data
        const result = data.filter(word => word.id ==id);
        dispatch({
            type: 'Get-GEMSTONE',
            payload: result,
        })
    })
}
export const getImage = () => (dispatch) => {
    axios.get(`${url}images`).then((res) => {
        //console.log(res.data)
        dispatch({
            type: 'Get-IMAGE',
            payload: res.data,
        })
    })
}
export const gethistory = (id) => (dispatch) => {
    axios.get(`${url}/history`).then((res) => {
        let data = res.data
      const result = data.filter(data => data.id ==id);
        dispatch({
            type: 'Get-HISTORY',
            payload:  res.data,
        })
    })
}
export const getclassfiction = (id) => (dispatch) => {
    axios.get(`${url}/classfiction`).then((res) => {
      let data = res.data
      const result = data.filter(word => word.id ==id);
      
        dispatch({
            type: 'Get-CLASSFICATION',
            payload: result,
        })
    })
}

