const initialState = {
    gemStone: [],
    images: [],
    history: [],
    classfication:[]
}
const gemstoneReducers = function(state=initialState,action){
    
    switch(action.type){
     
        case 'Get-GEMSTONE': {
            
            return {
                ...state,
                gemStone: [...action.payload],
            }
        }
        case 'Get-IMAGE': {
           
            return {
                ...state,
                images: [...action.payload],
            }
        }
        case 'Get-HISTORY': {
            
            return {
                ...state,
                history: [...action.payload],
            }
        }
        case 'Get-CLASSFICATION': {
           
            return {
                ...state,
                classfication: [...action.payload],
            }
        }
        
    //     case 'ADD-TODO':
    //    {
    //        console.log(action.payload.data[0].id)
    //     return {
    //         ...state,
    //         todoList: [action.payload],
    //     }
    //    }
    //     case 'DELETE-TODO':{
            // console.log(action.payload)
            // return {
            //     ...state,
            //     todoList: [action.payload],
            // }
        //}
        default: {
            return {
                ...state,
            }
        }
    }
}

export default gemstoneReducers